import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import { Link } from 'react-router-dom';
import { withAlert } from 'react-alert';  
import axios from 'axios';

class Register extends Component {

  //  UI
  renderField(field){
      const { meta : {touched, error} } = field;
      const hasError = `${touched && error ? 'has-error' : ''}` 

      return(
         <div>
            <div className={`form-group text-left ${hasError}`}>
               <label className="register-label">{field.label}</label>
               <input 
                  className="form-control input-lg register-field" 
                  type={field.type}
                  {...field.input}
               />
            </div>

            <div className="register-error">
               {touched ? error : ''}
            </div>
         </div>
      )
  }
  
  //  Form   ,    
  onSubmit(values){
      axios.post('/api/auth/register', values)
         .then( (res) => {
            if(res.data ==='DUPL-ID'){
                this.props.alert.show(' ID!');
            } 
            else if(res.data === 'DUPL-NICK'){
                this.props.alert.show(' !');
            } 
            else{
                window.alert(' .');
                this.props.history.push('/');
            }
         });
  }


  // ' ' UI 
  render(){
    const { handleSubmit } = this.props;

    return( 
      <div>

        <div className="register-title text-center">
          <p></p>
        </div>

        <div className="text-center">
          <form className="form-inline" onSubmit={ handleSubmit(this.onSubmit.bind(this)) }>
            <Field
              label=""
              name="id"
              type="text"
              component={this.renderField}
            />
            <Field
              label=""
              name="nickname"
              type="text"
              component={this.renderField}
            />
            <Field
              label=""
              name="password"
              type="password"
              component={this.renderField}
            />
            <Field
              label=" "
              name="repassword"
              type="password"
              component={this.renderField}
            />

            <div>
               <button 
                  className="btn btn-primary btn-lg register-submit" 
                  type="submit"
               >
               
               </button>
            </div>
            <div>
               <Link to="/" className="btn btn-danger btn-lg register-button"> </Link>
            </div>
          </form>
        </div>
        
      </div>
    )
  }
}

//    (validation)
function validate(values){
  
   const errors = {};
 
   if(!values.id){
     errors.id = " !";
   }

   if(values.id && values.id.length < 8){
      errors.id = "8  !";
   }

   if(!values.nickname){
     errors.nickname = " !";
   }

   if(!values.password){
     errors.password = " !";
   } 

   if(values.password && values.password.length < 8){
      errors.password = " 8  !";
   }

   if(values.password !== values.repassword){
      errors.repassword = "   !";
   }
   
   return errors;
 }

 //Redux-Form  Register  
export default reduxForm({
  validate,
  form: 'register'
})( withAlert(Register) );


