import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { fetchAllBooks } from '../actions';
import axios from 'axios';
import Slider from 'react-slick';
import Modal from './modal';


//Slick    '', ''  UI - PrevArrow, NextArrow
function PrevArrow(props){
      const { className, style, onClick } = props;
      return (
        <div
          className={className}
          style={{ ...style, display: "block"}}
          onClick={onClick}
        >
        </div>
      );
}

function NextArrow(props){
      const { className, style, onClick } = props;
      return (
        <div
          className={className}
          style={{ ...style, display: "block"}}
          onClick={onClick}
        >
        </div>
      );
}
    

class Home extends Component{

   constructor(props){
      super(props);
      this.state = {
         nickname: '',              // Nickname
         isClickImage: false,       //    -   (Modal)  
         
         //(Modal)   
         bookName: '',                   
         author: '',
         pubDate: '',
         memo: ''
      };

      this.clickImage = this.clickImage.bind(this);
      this.clickLogOut = this.clickLogOut.bind(this);
   }

   //  ,    / Redux Action     
   componentDidMount(){

      //     
      axios.get('/api/sessionInfo')
         .then( (res) => {
            if(res.data){
               this.setState({nickname: res.data.nickname});
            }
            else{
               window.alert(' !');
               this.props.history.push('/');
            }
      });

      //Redux Action -     
      if(!this.props.allbooks.length)
         this.props.fetchAllBooks();
   }

   //  -   ,     
   clickLogOut(){
      axios.get('/api/auth/logout')
            .then( (res) => {
               this.props.history.push('/');
            });
   }

   //    (Modal) , state    (Modal) state    
   clickImage(bookInfo){
      this.setState({
            isClickImage: !this.state.isClickImage,
            no: bookInfo.no,
            bookName: bookInfo.bookName,
            author: bookInfo.author,
            pubDate: bookInfo.pubDate,
            memo: bookInfo.memo
      });
   }

   // ()    
   //userBookInfo:     , nickname:  
   //userBookInfo : no(), ,  ,  , , , 
   renderImage(userBookInfo, nickname){

      if(userBookInfo){

            //Info:    ,     
            return userBookInfo.map( (Info) => {
                  return (
                        <div key={Info.bookImage} className="home-book-image-frame"> 
                        <img src={`/uploads/${nickname}/${Info.bookImage}`}
                              className="home-book-image"
                              alt={Info.bookImage}

                              //    
                              onClick={() => {this.clickImage(Info)}}
                        />
                        </div>
                  )
            })
      }
   }

   //    , Slick     
   renderUserBook(){
      const {allbooks} = this.props;
      
      //React-Slick API, responsive     
      const settings = {
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 1,
            nextArrow: <NextArrow />,
            prevArrow: <PrevArrow />,
            responsive: [
                  {
                        breakpoint: 1000,
                        settings: {
                              slidesToShow: 3,
                              slidesToScroll: 1
                        }
                  },
                  {
                        breakpoint: 800,
                        settings: {
                              slidesToShow: 2,
                              slidesToScroll: 1
                        }
                  },
                  {
                        breakpoint: 550,
                        settings: {
                              slidesToShow: 1,
                              slidesToScroll: 1
                        }
                  }
            ]
      };

      /*
      Reducer  allbooks(  )    UI 
      
        allbooks  ,
        allbooks = {
                        'nickname A': [ {no:'', nickname:'', bookName:'', bookImage:'', author:'', memo:''},
                                        {...},
                                        {...}
                                      ],
                        'nickname B': [ {...}, {...}, {...}],
                        ...                               
                   }
      */             
      return Object.getOwnPropertyNames(allbooks)
            .map( (nickname) => {
                  return(
                        <div key={nickname}>
                              <h2 className="home-book-nickname-frame">
                                    <span className="home-book-nickname">'{nickname}'</span>
                                    <span className="home-book-nickname-post">  </span>
                              </h2>                             
                              <Slider {...settings}>
                                    {this.renderImage(allbooks[nickname], nickname)}
                              </Slider>
                        </div>
                  );
            });
   }

   // ' ' UI 
   render(){
      return(
         <div>
            {/*   -   */}
            <div className="home-member text-right">
               <span>, <span className="home-nickname">{this.state.nickname}</span>!</span>
               <Link 
                  to={{
                       pathname: "/mypage",
                       state: {nickname: this.state.nickname}
                  }} 
                  className="home-member-btn btn btn-primary"
               >
                    
               </Link>
               <button
                  className="home-member-btn btn btn-danger" 
                  onClick={this.clickLogOut}
               >
                  
               </button>
            </div>

            {/*     */}
            <div className="home-book-list">
                  {this.renderUserBook()}
            </div>
            <div>
                  <Modal show={this.state.isClickImage}
                         onClose={ () => { 
                              this.setState({isClickImage: !this.state.isClickImage});

                              //       
                              document.querySelector('body').style.overflow = 'auto';
                              //document.querySelector('body').style.paddingRight = '0'; 
                         }}
                  >
                        {/* Modal  -    */}
                        <div>
                              <label className="home-book-text-title"> </label>
                              <p className="home-book-text-content">{this.state.bookName}</p>
                        </div>
                        <div>
                              <label className="home-book-text-title"></label>
                              <p className="home-book-text-content">{this.state.author}</p>
                        </div>
                        <div>
                              <label className="home-book-text-title"></label>
                              <p className="home-book-text-content">{this.state.pubDate}</p>
                        </div>
                        <div>
                              <label className="home-book-text-title"></label>
                              <p className="home-book-text-content">{this.state.memo}</p>
                        </div>
                  </Modal>
            </div>
         </div>
      );
   }
}

//Redux  allbooks Application-State 
function mapStateToProps(state) {
      return {
            allbooks: state.allbooks 
      }
}

//Redux  Home  
export default connect(mapStateToProps, { fetchAllBooks })(Home);


