import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import { withAlert } from 'react-alert';
import { Link } from 'react-router-dom';
import axios from 'axios';

class Login extends Component {

  //  ,       
  componentDidMount(){

    //      
    if(navigator.userAgent.toLowerCase().indexOf('trident')>-1){
        window.alert('   . < CHROME> !');
    }

    //    
    axios.get('/api/sessionInfo')
        .then( (res) => {
            if(res.data){
                window.alert('  , Home  .')
                this.props.history.push('/home'); 
            }
        });
  } 

  //  UI 
  renderField(field){

    const { meta : {touched, error} } = field;
    const hasError = `${touched && error ? 'has-error' : ''}`
    
    return(
        <div className={hasError}>
          <input 
            className={`form-control input-lg login-input ${hasError}`} 
            type={field.type} 
            placeholder={field.label}
            {...field.input}
          />

          <div className="login-error">
            {touched ? error : ''}
          </div>
        </div>
    )
  }
  
  //    ,       
  onSubmit(values){
    axios.post('/api/auth/login', values)
      .then( (res) => {
          if(res.data){
            if(res.data.isLogin)
                this.props.history.push('/home');
          }
          else{
            this.props.alert.show('()  () !');
          }
      });
  }

  // ' ' UI 
  render(){

    const { handleSubmit } = this.props;

    return(
        <div>
          <div className="text-center">
            <p className="login-title">SHARE READING</p>
            <p className="login-subtitle">    </p>
            <img src={ require("../images/login_welcome.jpg") } width="300px" alt="welcome"/>
          </div>

          <div className="form-group text-center">
            <form onSubmit={ handleSubmit(this.onSubmit.bind(this)) }>
              <Field
                label="ID"
                name="id"
                type="text"
                component={this.renderField}
              />
              <Field
                label="PASSWORD"
                name="password"
                type="password"
                component={this.renderField}
              />
              <button className="btn btn-primary btn-lg login-button" type="submit"></button>
            </form>
          </div>

          <div className="text-center">
            <div className="login-register">
              <Link to="/register" className="btn btn-danger btn-lg login-button"></Link>
            </div>
          </div>

        </div>
    )
  }
}

//Redux-Form   (Validation)
function validate(values){

    const errors = {};

    if(!values.id){
        errors.id = " !";
    }

    if(values.id && values.id.length < 8){
        errors.id = "8  !";
    }

    if(!values.password){
        errors.password = " !";
    }

    if(values.password && values.password.length < 8){
        errors.password = "8  !";
    }

    return errors;
}

//Redux-Form   
export default reduxForm({
  validate,
  form: 'login'
})( withAlert(Login) );


